<template>
  <div>
    <div class="searchs">
      <el-form :inline="true" @submit.native.prevent="onSubmit" :model="searchs" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="账号">
              <el-input v-model="searchs.keyword_search_value" placeholder="会员手机号"></el-input>
            </el-form-item>
            <el-form-item label="状态">
              <el-select v-model="searchs.status" clearable placeholder="请选择">
                <el-option v-for="(value, name) in config.status" :key="name" :label="value" :value="name"> </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="交易日期">
              <el-date-picker
                v-model="addtime"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="pickerOptions"
              />
            </el-form-item>
            <el-form-item>
              <el-button type="primary" native-type="submit">查询</el-button>
              <el-button type="danger" @click="removeAll">批量删除</el-button>
            </el-form-item>
          </el-col>
          <el-col>
            提现成功：<span style="color: red;">{{ data.sum_ok_money }}元</span> 提现失败：<span style="color: red;"
              >{{ data.sum_no_money }}元</span
            >
            未处理：<span style="color: red;">{{ data.sum_on_money }}元</span>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column prop="id" label="订单号" align="center" />
      <el-table-column prop="name" label="姓名" align="center" />
      <el-table-column prop="remark" label="管理员备注" align="center" />
      <el-table-column prop="mobile" label="账号" align="center" width="150" />
      <!--            <el-table-column-->
      <!--                    prop="invest_money"-->
      <!--                    label="总充值"-->
      <!--                    align="center"-->
      <!--            />-->
      <!--            <el-table-column-->
      <!--                    prop="cost_money"-->
      <!--                    label="总提现"-->
      <!--                    align="center"-->
      <!--            />-->
      <el-table-column prop="money" label="提现金额" align="center" />
      <el-table-column prop="bank_name" label="银行" align="center" />
      <el-table-column prop="bank_card" label="银行卡号" align="center" />
      <el-table-column prop="status_name" label="状态" align="center" />
      <el-table-column prop="addtime_date" label="申请时间" align="center" width="170" />
      <el-table-column label="操作" width="280">
        <template slot-scope="scope">
          <el-button
            v-if="scope.row.status === 'S' || scope.row.status === 'D'"
            @click="setSuccess(scope.row)"
            type="success"
            size="mini"
            >成功
          </el-button>
          <el-button
            v-if="scope.row.status === 'S' || scope.row.status === 'D'"
            @click="setNot(scope.row)"
            type="warning"
            size="mini"
            >失败
          </el-button>
          <el-button v-if="scope.row.status === 'S'" @click="setMiss(scope.row)" type="primary" size="mini">
            忽略
          </el-button>
          <!--                    <el-button v-if="scope.row.status === 'S' || scope.row.status === 'D'" @click="cancel(scope.row)"-->
          <!--                               type="danger" size="mini">取消-->
          <!--                    </el-button>-->
          <el-col v-if="scope.row.status === 'Y' || scope.row.status === 'N' || scope.row.status === 'C'">无</el-col>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
    <el-dialog title="会员详情" :visible.sync="dialogFormVisible">
      <el-form :model="detail">
        <el-form-item label="活动名称" label-width="80">
          <el-input v-model="detail" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import dayjs from 'dayjs'
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      dialogFormVisible: false,
      searchs: {
        keyword_search_value: '',
        begin_addtime: '',
        end_addtime: '',
        status: '',
      },
      config: {
        status: {},
      },
      loading: false,
      detail: {},
      data: '',
      tableData: [],
      count: 0,
      page_curren: 1,
      addtime: [],
      pickerOptions: {
        shortcuts: [
          {
            text: '本月',
            onClick(picker) {
              picker.$emit('pick', [new Date(), new Date()])
            },
          },
          {
            text: '今年至今',
            onClick(picker) {
              const end = new Date()
              const start = new Date(new Date().getFullYear(), 0)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '最近六个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setMonth(start.getMonth() - 6)
              picker.$emit('pick', [start, end])
            },
          },
        ],
      },
    }
  },
  watch: {
    addtime(val) {
      if (val) {
        this.searchs.begin_addtime = dayjs(val[0]).format('YYYY-MM-DD')
        this.searchs.end_addtime = dayjs(val[1]).format('YYYY-MM-DD')
      } else {
        this.searchs.begin_addtime = ''
        this.searchs.end_addtime = ''
      }
    },
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    FetchList() {
      this.loading = true
      Fetch('/fin/cost/search', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.data = r.data
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.config = r.data.config
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    setSuccess(row) {
      this.$confirm('是否确认将状态修改为成功?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            Fetch('/fin/cost/verify', { id: row.id, status: 'Y' })
              .then(() => {
                instance.confirmButtonLoading = false
                Message.success('操作成功')
                this.FetchList()
                done()
              })
              .catch(() => {
                instance.confirmButtonLoading = false
                Message.error('操作失败')
              })
          } else {
            done()
          }
        },
      })
    },
    // setNot(row) {
    //   this.$confirm('是否确认将状态修改为失败?', undefined, {
    //     type: 'warning',
    //     beforeClose: (action, instance, done) => {
    //       if (action === 'confirm') {
    //         instance.confirmButtonLoading = true
    //         Fetch('/fin/cost/verify', { id: row.id, status: 'N' })
    //           .then(() => {
    //             instance.confirmButtonLoading = false
    //             Message.success('操作成功')
    //             this.FetchList()
    //             done()
    //           })
    //           .catch(() => {
    //             instance.confirmButtonLoading = false
    //             Message.error('操作失败')
    //           })
    //       } else {
    //         done()
    //       }
    //     },
    //   })
    // },
    setNot(row) {
      this.$prompt('请输入失败原因', '是否确认将状态修改为失败?', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
      }).then(({ value }) => {
        Fetch('/fin/cost/verify', { id: row.id, status: 'N', fail_tips: value })
          .then(() => {
            Message.success('操作成功')
            this.FetchList()
          })
          .catch(() => {
            Message.error('操作失败')
          })
      })
    },
    setMiss(row) {
      this.$confirm('是否确认将状态修改为忽略?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            Fetch('/fin/cost/verify', { id: row.id, status: 'D' })
              .then(() => {
                instance.confirmButtonLoading = false
                Message.success('操作成功')
                this.FetchList()
                done()
              })
              .catch(() => {
                instance.confirmButtonLoading = false
                Message.error('操作失败')
              })
          } else {
            done()
          }
        },
      })
    },
    cancel(row) {
      this.$confirm('此操作将会直接使订单失效, 是否继续?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }).then(() => {
        this.loading = true
        Fetch('/fin/cost/verify', { id: row.id, status: 'C' })
          .then(() => {
            this.loading = false
            Message.success('操作成功')
            this.FetchList()
          })
          .catch(() => {
            this.loading = false
          })
      })
    },
    removeAll() {
      if (this.ids.length < 1) {
        Message.error('未选中数据')
        return
      }
      this.loading = true
      Fetch('/fin/cost/remove', { id: this.ids })
        .then(() => {
          Message.success('删除成功')
          this.FetchList()
          this.loading = false
        })
        .catch(() => (this.loading = false))
    },
    handleSelectionChange(val) {
      this.ids = []
      if (val) {
        val.forEach(item => {
          this.ids.push(item.id)
        })
      }
    },
  },
}
</script>
